.page {
    font-size: 18px;
    min-height: 100vh;
    text-align: left;
}

.content h1 {
    margin: 30px 0px;
    color:salmon;
}

.content {
    padding-top: 100px;
    margin: 0px auto;
    width: 80%;
}

@media only screen and (min-width: 700px) {
    .content {
        padding-top: 120px;
    }
}

@media only screen and (min-width: 950px) {
    .content {
        width: 750px;
    }
}

.content > div {
    margin-bottom: 80px;
}
