.header {
    margin: 20px 0px;
    font-weight: bold;
}

.header div {
    margin-bottom: 10px;
}

.duration {
    font-weight: 400;
    font-size: 15px;
}

@media only screen and (min-width: 750px) {
    .header {
        display: flex;
        justify-content: space-between;
    }
    .header div {
        margin-bottom: 0px;
    }
    .duration {
        font-weight: bold;
        font-size: 18px;
    }
}