.page {
    font-size: 18px;
    min-height: 100vh;
}

.content h1 {
    margin: 30px 0px 50px 0px;
    color:salmon;
}

.content {
    padding-top: 90px;
    padding-bottom: 70px;
    margin: 0px auto;
    width: 80%;
}

.cardsContainer {
    display: grid;
    row-gap: 80px;
    grid-gap: 50px;
    grid-template-columns: repeat(auto-fill, 280px);
    justify-content: space-evenly;
}