.page {
    font-size:18px;
    min-height: 100vh;
}

.content h1 {
    color:salmon;
}

.content {
    padding: 120px 0px 50px 0px;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

@media only screen and (min-width: 1050px) {
    .content {
        padding: 170px 0px 80px 0px;
        height: auto;
        flex-direction: row;
        align-items: flex-start;
        width: 900px;
    }
}

@media only screen and (min-width: 300px) {
    .textContainer {
        width: 300px;
    }
}

.textContainer {
    text-align: center;
    margin-bottom: 50px;
}


.form {
    height: 600px;
    width: 95%;
    padding: 10%;
    background-color: white;
    border: 1px solid #ccc;
    box-shadow: 0px 5px 5px 5px #eee;
    border-radius: 5px;
    box-sizing: border-box;
}

@media only screen and (min-width: 500px) {
    .form {
        width: 450px;
        padding: 30px 80px;
    }
}

.formHeader h1{
    margin: 0px;
}
