.button {
    height: 40px;
    width: 100px;
    border-radius: 5px;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    outline: none;
    transition: 0.5s;
}

.button:hover {
    transform: translateY(-2px);
    box-shadow: 0 0px 30px salmon;
}

.button a {
    text-decoration: none;
}

.contact {
    width: 100%;
}