a {
    text-decoration: none;
    color: inherit;
}

.card {
    width: 100%;
    background-color: white;
    border: 1px solid #ccc;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    font-size: 16px;
}

.card:hover {
    box-shadow: 0px 5px 5px 5px #ddd;
}

.imgContainer {
    height: 187px;
    border-bottom: 1px solid #ccc;
    box-shadow: 0px 1px 1px 1px #eee;
}


.card img {
    object-fit: cover;
    width: 100%;
    height: auto;
}

