.page {
    height: 100vh;
    padding: 0px 10%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 22px;
}

.content {
    width: 100%;
}

.content h1 {
    margin: 50px 0px 0px 0px;
    color:salmon;
}

.introTextContainer {
    height: 150px;
    margin: 30px 0px;
}

@media only screen and (min-width: 360px) {
    .introTextContainer {
        margin: 50px 0px 0px 0px;
    }
    .iconsContainer {
        width: 300px;
    }
}


@media only screen and (min-width: 650px) {
    .content {
        width: 500px;
    }
}

.iconsContainer {
    width: 100%;
    margin: 0px auto 30px auto;
    display: flex;
    justify-content: space-evenly;
}

.btnContainer {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}